window.fetch = undefined;
require('whatwg-fetch');

var trezor = require('trezor.js');
var Promise = require('es6-promise').Promise;

function onMessage(event) {
    var request = event.data;
    if (!request) {
        return;
    }

    switch (request.type) {

    case 'ping':
        handlePing(event);
        break;

    case 'login':
        handleLogin(event);
        break;

    case 'xpubkey':
        handleXpubKey(event);
        break;

    case 'signtx':
        handleSignTx(event);
        break;

    default:
        console.warn('Unknown message', request);
    }
}

window.addEventListener('message', onMessage);

/*
 * 'ping'
 */

function handlePing(event) {
    event.source.postMessage({
        success: true,
        type: 'ping'
    }, event.origin)
}

/*
 * 'login'
 */

function handleLogin(event) {
    var request = loginRequest(event);

    displaySelector('.s_login');
    document.getElementById('login_origin').innerHTML = event.origin;
    document.getElementById('login_icon').src = request.icon || './trezor.png';

    initialize()
        .then(function (device) { // send SignIdentity
            return device.session.signIdentity(
                request.identity,
                request.challenge_hidden,
                request.challenge_visual
            );
        })

        .then(function (result) { // success
            var response = {
                success: true,
                challenge_hidden: request.challenge_hidden,
                challenge_visual: request.challenge_visual,
                address: result.message.address,
                public_key: result.message.public_key,
                signature: result.message.signature
            };
            event.source.postMessage(response, event.origin);
            window.close();
        })

        .catch(function (error) { // failure
            console.error(error);
            var response = {
                success: false,
                error: error.message
            };
            event.source.postMessage(response, event.origin);
            window.close();
        }).done();
}

function loginRequest(event) {
    var request = event.data;
    var loc = event.origin.split(':');

    request.identity = {};
    request.identity.proto = loc[0];
    request.identity.host = loc[1].substring(2);
    if (loc[2]) {
        request.identity.port = loc[2];
    }
    request.identity.index = 0;

    return request;
}

/*
 * 'xpubkey'
 */

function handleXpubKey(event) {
    var path = event.data.path;

    displaySelector('.s_xpubkey');
    useEmptyPassphrase = false; // prompt use for passphrase

    initialize()
        .then(function (device) {
            return promptXpubPermission(path).then(function () {
                return device.session.getPublicKey(path);
            });
        })

        .then(function (result) { // success
            var message = result.message;
            var xpub = message.xpub;

            if (!xpub) {
                throw new Error('Please upgrade your device firmware');
            }
            var response = {
                success: true,
                xpubkey: xpub
            };
            event.source.postMessage(response, event.origin);
            window.close();
        })

        .catch(function (error) { // failure
            console.error(error);
            var response = {
                success: false,
                error: error.message
            };
            event.source.postMessage(response, event.origin);
            window.close();
        }).done();
}

function promptXpubPermission(path) {
    return new Promise(function (resolve, reject) {
        var e = document.getElementById('xpubkey_id');
        e.innerHTML = xpubKeyLabel(path);
        e.callback = function (cancel) {
            if (cancel) {
                reject(new Error('Cancelled'));
            } else {
                resolve(path);
            }
        }
        displaySelector('#xpubkey_info');
        displaySelector('#xpubkey_actions');
    });
}

function exportXpubKey() {
    var e = document.getElementById('xpubkey_id');
    e.callback();
}

window.exportXpubKey = exportXpubKey;

function cancelXpubKey() {
    var e = document.getElementById('xpubkey_id');
    e.callback(true);
}

window.cancelXpubKey = cancelXpubKey;

var HD_HARDENED = 0x80000000;

function xpubKeyLabel(path) {
    var hardened = function (i) { return path[i] & ~HD_HARDENED; };
    switch (hardened(0)) {
    case 44:
        var index = hardened(2) + 1;
        return 'Account #' + index;
    case 45:
        return 'HD multisig wallet';
    default:
        return serializePath(path);
    }
}

function serializePath(path) {
    return path.map(function (i) {
        var s = (i & ~HD_HARDENED).toString();
        if (i & HD_HARDENED) {
            return s + "'";
        } else {
            return s;
        }
    }).join('/');
}

/*
 * 'signtx'
 */

function handleSignTx(event) {
    var inputs = event.data.inputs;
    var outputs = event.data.outputs;
    var coinName = 'Bitcoin';

    displaySelector('.s_signtx');

    initialize()
        .then(function (device) {
            var coin = device.getCoin(coinName);
            renderStatus('Looking up transactions...');
            return lookupReferencedTxs(inputs).then(function (txs) {
                renderStatus('Signing transaction...');
                return device.session.signTx(inputs, outputs, txs, coin);
            });
        })

        .then(function (result) { // success
            var message = result.message;
            var response = {
                success: true,
                type: 'signtx',
                signatures: message.serialized.signatures,
                serialized_tx: message.serialized.serialized_tx
            };
            event.source.postMessage(response, event.origin);
            window.close();
        })

        .catch(function (error) { // failure
            console.error(error);
            var response = {
                success: false,
                error: error.message
            };
            event.source.postMessage(response, event.origin);
            window.close();
        }).done();
}

function lookupReferencedTxs(inputs) {
    return Promise.all(inputs.map(function (input) {
        return lookupTx(input.prev_hash);
    }));
}

var INSIGHT_URL = 'https://insight.bitpay.com';

function lookupTx(hash) {
    return fetch(INSIGHT_URL + '/api/tx/' + hash)
        .then(function (response) {
            if (response.status === 200) {
                return response;
            } else {
                throw new Error(response.statusText);
            }
        })
        .then(function (response) { return response.json(); })
        .then(insightTxToTrezorTx);
}

/**
 * Converts Insight API result to TREZOR `TransactionType`
 * with `bin_outputs`.
 * @see https://github.com/bitpay/insight-api
 * @see https://github.com/trezor/trezor-common/blob/master/protob/types.proto
 */
function insightTxToTrezorTx(result) {
    return {
        hash: result.txid,
        version: result.version,
        lock_time: result.locktime,

        inputs_cnt: result.vin.length,
        inputs: result.vin.map(function (input) {
            return {
                prev_hash: input.txid,
                prev_index: input.vin >>> 0,    // can be -1 in coinbase
                sequence: input.sequence >>> 0, // usually -1, 0 in coinbase
                script_sig: input.scriptSig.hex
            };
        }),

        outputs_cnt: result.vout.length,
        bin_outputs: result.vout.map(function (output) {
            var amount = (output.value * 1e8) | 0;
            return {
                amount: amount,
                script_pubkey: output.scriptPubKey.hex
            };
        })
    };
}

/*
 * Devices
 */

function initialize() {
    return trezor
        .loadTransport()
        .then(configureTransport)
        .then(waitForFirstDevice);
}

function configureTransport(transport) {
    var CONFIG_URL = './config_signed.bin';

    return trezor.http(CONFIG_URL)
        .then(function (config) { return transport.configure(config); })
        .then(function () { return transport; });
}

var noConnectedDevices = new Error();
var deviceIsBootloader = new Error();

function waitForFirstDevice(transport) {
    var WAIT_BEFORE_RETRY = 3000;

    var retryWait = function () {
        return resolveAfter(WAIT_BEFORE_RETRY).then(function () {
            return waitForFirstDevice(transport);
        });
    };

    return getFirstDevice(transport)
        .then(function (device) {
            if (device.isBootloader()) {
                throw deviceIsBootloader;
            }
            return device;
        })
        .catch(function (error) {
            if (error === noConnectedDevices) {
                renderStatus('Please connect your TREZOR device.');
                return retryWait();
            }
            if (error === deviceIsBootloader) {
                renderStatus('Please re-connect your device.');
                return retryWait();
            }
            throw error;
        });
}

function getFirstDevice(transport) {
    return transport.enumerate().then(function (descriptors) {
        if (descriptors.length > 0) {
            return Device.fromDescriptor(
                transport,
                descriptors[0]
            );
        } else {
            throw noConnectedDevices;
        }
    });
}

/**
 * @constructor
 * @param {trezor.Session}
 * @param {Features}
 */
function Device(session, features) {
    this.session = session;
    this.features = features;

    this.session.on('passphrase', passphraseCallback);
    this.session.on('button', buttonCallback);
    this.session.on('pin', pinCallback);
}

/**
 * @param {trezor.Transport}
 * @param {trezor.Descriptor}
 * @return {Promise<Device>}
 */
Device.fromDescriptor = function (transport, descriptor) {
    return Device.acquireSession(transport, descriptor).then(function (session) {
        return Device.fromSession(session);
    });
};

/**
 * @param {trezor.Session}
 * @return {Promise<Device>}
 */
Device.fromSession = function (session) {
    return session.initialize().then(function (result) {
        return new Device(session, result.message);
    });
};

/**
 * @param {trezor.Transport}
 * @param {trezor.Descriptor}
 * @return {Promise<trezor.Session>}
 */
Device.acquireSession = function (transport, descriptor) {
    return transport.acquire(descriptor).then(function (result) {
        return new trezor.Session(transport, result.session);
    });
};

/**
 * @return {boolean}
 */
Device.prototype.isBootloader = function () {
    return this.features.bootloader_mode;
};

/**
 * @param {string}
 * @return {CoinType}
 */
Device.prototype.getCoin = function (name) {
    var coins = this.features.coins;

    for (var i = 0; i < coins.length; i++) {
        if (coins[i].coin_name === name) {
            return coins[i];
        }
    }

    throw new TypeError('Coin "' + name + '" not found');
};

/*
 * Buttons
 */

function buttonCallback(code) {
    renderStatus('Confirm action on your device ...');
}

/*
 * PIN
 */

function pinCallback(type, callback) {
    renderPinDialog(type, callback);
}

function renderPinDialog(type, callback) {
    var e = document.getElementById('pin_dialog');
    e.style.display = 'block';
    e.callback = callback;
    window.addEventListener('keydown', pinKeydownHandler);
}

function pinKeydownHandler(e) {
    var KEY_BACKSPACE = 8;
    var KEY_ENTER = 13;
    var k = e.keyCode;

    if (k === KEY_BACKSPACE) {
        pinBackspace();

    } else if (k === KEY_ENTER) {
        pinEnter();

    } else if (isNumericKey(k)) {
        var num = getNumberFromKey(k);
        var str = String.fromCharCode(num);
        pinAdd(str);
    }
}

function isNumericKey(k) {
    return (k >= 49 && k <= 57) || (k >= 97 && k <= 105);
}

function getNumberFromKey(k) {
    return (k >= 97) ? (k - (97 - 49)) : k;
}

function pinAdd(num) {
    var e = document.getElementById('pin');
    e.value += num;
}

window.pinAdd = pinAdd;

function pinBackspace() {
    var e = document.getElementById('pin');
    e.value = e.value.slice(0, -1);
}

window.pinBackspace = pinBackspace;

function pinEnter() {
    var e = document.getElementById('pin');
    var pin = e.value;
    e = document.getElementById('pin_dialog');
    e.style.display = 'none';
    e.callback(null, pin);
    window.removeEventListener('keydown', pinKeydownHandler);
}

window.pinEnter = pinEnter;

/*
 * Passphrase
 */

var useEmptyPassphrase = true;

function passphraseCallback(callback) {
    if (useEmptyPassphrase) {
        callback(null, '');
    } else {
        renderPassphraseDialog(callback);
    }
}

function renderPassphraseDialog(callback) {
    var e = document.getElementById('passphrase_dialog');
    e.style.display = 'block';
    e.callback = callback;
    e = document.getElementById('passphrase');
    e.focus();
}

function passphraseToggle() {
    var e = document.getElementById('passphrase');
    e.type = (e.type === 'text') ? 'password' : 'text';
}

window.passphraseToggle = passphraseToggle;

function passphraseEnter() {
    var e = document.getElementById('passphrase');
    var passphrase = e.value;
    e = document.getElementById('passphrase_dialog');
    e.style.display = 'none';
    e.callback(null, passphrase);
}

window.passphraseEnter = passphraseEnter;

/*
 * Utils
 */

function forEach(xs, fn) {
    for (var i = 0; i < xs.length; i++) {
        fn(xs[i], i, xs);
    }
    return xs;
}

function removeClass(e, c) {
    e.className = e.className.replace(c, '');
}

function addClass(e, c) {
    e.className = e.className + ' ' + c;
}

function hideSelector(selector) {
    return forEach(document.querySelectorAll(selector), function (e) {
        addClass(e, 'hidden');
    });
}

function displaySelector(selector) {
    return forEach(document.querySelectorAll(selector), function (e) {
        removeClass(e, 'hidden');
    });
}

function renderStatus(string) {
    var e = document.getElementById('status_bar');
    e.innerHTML = string;
}

function resolveAfter(msec) {
    return new Promise(function (resolve) {
        setTimeout(resolve, msec);
    });
}
